* {
  margin: 0;
  padding: 0;
}

html {
  background: #000000;
}

.webgl {
  position: fixed;
  top: 0;
  left: 0;
  outline: none;
}


.body {
  height: 300vh;
}

h1 {
  font-family: 'Ubuntu mono', monospace;
  color: #ffeded;
  /* text-transform: uppercase; */
  font-size: 6vmin;
  padding-top: 30%;
  /* display:inline; */
}

.section {

  align-items: center;
  height: 100vh;
  /* position: relative; */


}

p {
  /* position:; */
  /* display:flex; */
  font-family: 'Ubuntu mono', monospace;
  font-size: 3vmin;
  color: #ffeded;
  /* justify-content:flex-start; */
}


section:nth-child(odd) {
  text-align: left;
  padding-left: 10%;
  padding-right: 50%;
}

section:nth-child(even) {
  text-align: right;
  padding-left: 50%;
  padding-right: 10%;
}

/* Add a black background color to the top navigation */
.topnav {
  background-color: rgb(0, 0, 0);
  overflow: hidden;
  align-self: center;
  position: sticky;
  display: block;
  align-content: center;
}

/* Style the links inside the navigation bar */
.topnav a {
  float: right;
  color: #f2f2f2;
  text-align: center;
  padding: 14px 24px;
  font-family: 'Ubuntu mono', monospace;
  text-decoration: none;
  font-size: 17px;
}

/* Change the color of links on hover */
.topnav a:hover {
  background-color: #ddd;
  color: rgb(32, 32, 32);
}

/* Add a color to the active/current link */
.topnav a.active {
  background-color: #424242;
  color: white;
}

/* Button */
.middle {
  /* position: absolute; */
  top: 50%;
  left: 50%;
  /* transform: translate(-50%,-50%); */
}

.btn {
  position: absolute;
  display: block;
  color: white;
  font-size: 3vmin;
  font-family: "Ubuntu mono", monospace;
  text-decoration: none;
  margin: 30px 0;
  border: 2px solid #1abd22;
  padding: 14px 60px;
  /* text-transform: uppercase; */
  overflow: hidden;
  transition: 1s all ease;
}

.btn::before {
  background: #1abd22;
  color: black;
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: -1;
  transition: all 0.6s ease;
}

.btn1::before {
  width: 0%;
  height: 100%;
}

.btn1:hover::before {
  width: 100%;
}